import { useEffect, useState } from "react";
import { fetchPdfData } from "../../services/pdfData";
import { getItem, StoreEnum } from "../../utils/store";

interface IData {
    scaledScore: number,
    subItemsScore: number[],
    subDomainScore: number[],
    subDomainX: string[],
    subDomainY: number[][],
    scaledScoresTrend: number[],
    testDate: string,
    lexileMeasure:string
}

export const usePdfData = () => {

    const [loading, setLoading] = useState(true);

    const [data, setData] = useState<IData>({
        scaledScore: 0,
        subItemsScore: [],
        subDomainScore: [],
        subDomainX: [],
        subDomainY: [],
        scaledScoresTrend: [],
        testDate: '',
        lexileMeasure:''
    });
    useEffect(() => {
        const phone = getItem(StoreEnum.PHONE) || '19951819122';
        fetchPdfData(phone).then((res) => {
            if (res && res.executed) {
                const tempArr = res.sub_domain_score_trend_value;
                const subDomainY: number[][] = [];
                new Array(10).fill(10).forEach((v, i) => {
                    subDomainY[i] = [];
                });
                tempArr.forEach((v, i) => {
                    v.forEach((vv, ii) => {
                        subDomainY[ii].push(vv);
                    })
                })
                setData({
                    scaledScore: res.scaled_score,
                    subItemsScore: res.sub_items_score,
                    subDomainScore: res.sub_domain_score,
                    subDomainX:res.scaled_scores_trend_date,
                    subDomainY,
                    scaledScoresTrend: res.scaled_scores_trend_value,
                    lexileMeasure: res.lexile_measure,
                    testDate:res.test_date
                })
            }
        }).finally(() => {
            setLoading(false);
        })
    }, []);

    return {
        data,
        loading
    }
}